
  import { Component } from '@angular/core';
  import { IonicPage, NavController, NavParams } from 'ionic-angular';
  import { EventProvider } from '../../providers/event/event';


@IonicPage(
{
segment: 'anuncio-detalhes/:eventId'

}
)

@Component({
  selector: 'page-anuncio-detalhes',
  templateUrl: 'anuncio-detalhes.html',
})
 

  export class AnuncioDetalhesPage {
      public currentEvent: any = {};
  
      constructor(public navCtrl: NavController,  public navParams: NavParams, 
          public eventProvider: EventProvider) 
          { 
              this.currentEvent = this.eventProvider.getEventDetail;
          }
      ionViewDidLoad() {
          this.eventProvider.getEventDetail(this.navParams.get('eventId')).on('value', eventSnapshot => 
              {
                  this.currentEvent = eventSnapshot.val();
                  this.currentEvent.id = eventSnapshot.key;
              });         

      }
}



